import React, { Component } from "react";

import { Row, Col, Card, CardBody, FormGroup, Button,  Label, Container, } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";
import Select from "react-select";
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'
//Import Breadcrumb
import Breadcrumbs from '../../components/Common/Breadcrumb';
import { BaseUrl } from "../../helpers/Baseurl"

const optionGroup = [
	{
		label: "Status",
		options: [
			{ label: "Todo", value: "todo" },
			{ label: "Completed", value: "completed" },
			{ label: "Pending", value: "pending" }
		]
	}
];

class FormValidations extends Component {
  constructor(props) {
    super(props);
    this.state = {
      breadcrumbItems: [
        { title: "Jobs", link: "/jobs" },
        { title: "Add New Job", link: "#" },
      ],
      selectedGroup: { label: "Todo", value: "todo" },
    };
    toastr.options = {
      positionClass: "toast-top-right",
      timeOut: "true",
      closeButton: "true",
      progressBar: "true",
      showEasing: "swing",
      hideEasing: "linear",
      showMethod: "fadeIn",
      hideMethod: "fadeOut",
      showDuration: 300,
      hideDuration: 1000,
      timeOut: 5000,
      extendedTimeOut: 1000

  }
	

    this.handleSubmit = this.handleSubmit.bind(this);
    this.changeHandeler.bind(this);;
    this.handleSelectGroup = this.handleSelectGroup.bind(this);

  }
  

  handleSelectGroup = selectedGroup => {
    this.setState({ selectedGroup });
  };


  handleSubmit(e, values) {
    
    const { selectedGroup } =  this.state
    values['status'] = selectedGroup.value
    console.log(values, "----------values")
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(values)
        
        };
        fetch(`${BaseUrl}/api/jobs/jobs/`,requestOptions)
              .then(res => 
                  {if(res.status == 400||res.status == 404 || res.status == 500){
                      res.json()
                      .then(
                          (data) =>{  
                            console.log(data, "---data")
                            toastr.error("Something Went Wrong", "")  }
                      )
                  }
                  else{
                      res.json()
                      .then(
                          (data) =>{
                          toastr.success(`Job Created Successfully`, "") 
                          this.props.history.push(`/edit-job/${data.id}`) 
                          }
                      )
                  }    
              }
          )

  }

  //for change tooltip display propery
  changeHandeler(event, eleId) {
    if (event.target.value !== "")
      document.getElementById(eleId).style.display = "none";
    else
      document.getElementById(eleId).style.display = "block";
  }


  render() {
    const { selectedGroup } = this.state;
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid={true}>
            <Breadcrumbs title="Add New Job" breadcrumbItems={this.state.breadcrumbItems} />
            <AvForm className="needs-validation" onValidSubmit={this.handleSubmit} >
              <Row>
                <Col xl="6">
                  <Card>
                    <CardBody> 
                      <h4 className="card-title">Job Detail</h4>
                        <Row>
                          <Col md="6">
                            <div className="mb-3">
                              <Label className="form-label" htmlFor="name">Job Name</Label>
                              <AvField
                                name="name"
                                placeholder="Job Name"
                                type="text"
                                errorMessage="Enter First Name"
                                className="form-control"
                                validate={{ required: { value: true } }}
                                id="name"
                              />
                            </div>
                          </Col>
                  
                          <Col md="6">
                            <div className="mb-3">
                                <AvField
                                name="labour_rate"
                                label="Labor Rate"
                                placeholder="Labor Rate"
                                type="number"
                                errorMessage="Enter Only Number"
                                validate={{
                                    required: { value: true },
                                    pattern: {
                                    value: "^[0-9]+$",
                                    errorMessage: "Only Numbers"
                                    }
                                }}
                                />
                            </div>
                          </Col>
                        </Row>
                        <Row>
                          <Col md="6">
                            <div className="mb-3">
                              <AvField
                                name="desired_gross_margin"
                                label="Desired Gross Margin"
                                placeholder="Desired Gross Margin"
                                type="number"
                                errorMessage="Enter Only Number"
                                validate={{
                                    required: { value: true },
                                    pattern: {
                                    value: "^[0-9.]+$",
                                    errorMessage: "Only Numbers"
                                    }
                                }}
                              />
                            </div>
                          </Col>
                          <Col md="6">
                              <div className="mb-3">
                                <AvField
                                  name="actual_gross_margin"
                                  label="Actual Gross Margin"
                                  placeholder="Actual Gross Margin"
                                  type="number"
                                  errorMessage="Enter Only Number"
                                  validate={{
                                      pattern: {
                                      value: "^[0-9.]+$",
                                      errorMessage: "Only Numbers"
                                      }
                                  }}
                                />
                              </div>
                          </Col>
                        </Row>
                        <Row>
                          <Col md="6">
                            <div className="mb-3">
                              <AvField
                                name="target_selling_price"
                                label="Target Selling Price"
                                placeholder="Target Selling Price"
                                type="number"
                                errorMessage="Enter Only Number"
                                value={"0"}
                                validate={{
                                    pattern: {
                                    value: "^[0-9.]+$",
                                    errorMessage: "Only Numbers"
                                    }
                                }}
                              />
                            </div>
                          </Col>
                          <Col md="6">
                            <div className="mb-3">
                              <AvField
                                name="actual_selling_price"
                                label="Actual Selling Price"
                                placeholder="Actual Selling Price"
                                type="number"
                                errorMessage="Enter Only Number"
                                validate={{
                                    required: { value: true },
                                    pattern: {
                                    value: "^[0-9.]+$",
                                    errorMessage: "Only Numbers"
                                    }
                                }}
                              />
                            </div>
                          </Col>
                        </Row>
                        <Row>
                          <Col md="6">
                            <div className="mb-3">
                              <Label className="form-label">Job Status</Label>
                              <Select 
                                value={selectedGroup}
                                onChange={this.handleSelectGroup}
                                options={optionGroup}
                                classNamePrefix="select2-selection"
                              />

                            </div>
                          </Col>
                        </Row>  
                        <FormGroup className="mb-0">
                        <div>
                          <Button type="submit" color="primary" className="me-1">
                            Submit
                        </Button>{" "}
                          <Button type="button" onClick={() => this.props.history.goBack()} color="secondary">
                            Cancel
                        </Button>
                        </div>
                      </FormGroup>      

                    </CardBody>
                  </Card>
                </Col>
              </Row>
            </AvForm>

          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default FormValidations;



    