import React, { Component } from "react"
import PropTypes from "prop-types"
import { connect } from "react-redux"
import { Button, Card, CardBody, Col, Container, Row } from "reactstrap"
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit"
import BootstrapTable from "react-bootstrap-table-next"
import paginationFactory, {
  PaginationListStandalone,
  PaginationProvider,
} from "react-bootstrap-table2-paginator"

import { Link } from "react-router-dom"

//Import Breadcrumb
import Breadcrumbs from "../../../components/Common/Breadcrumb"

import { BaseUrl } from "../../../helpers/Baseurl"

import {
  getCustomers
} from "../../../store/e-commerce/actions"

class EcommerceCustomers extends Component {
  constructor(props) {
    super(props)
    if (localStorage.getItem("authUser")) {
      const obj = JSON.parse(localStorage.getItem("authUser"));
      const is_superuser = obj.user.is_superuser
      if (!is_superuser){
        this.props.history.push('/dashboard')
        }
    }
    this.state = {
      customers: [],
      breadcrumbItems: [
          { title : "DynamicOutdoors", link : "/dashboard" },
          { title : "Users", link : "#" }
      ]
    }

    this.toggle = this.toggle.bind(this)
    this.handleCustomerClicks = this.handleCustomerClicks.bind(this)
    this.handleDelete = this.handleDelete.bind(this)
    this.handleEdit = this.handleEdit.bind(this)

  }
  

  async componentDidMount() {
 
     const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        };

        await fetch(`${BaseUrl}/api/account/users/`,requestOptions)
            .then(res => res.json())
            .then(
                (data) => {
                    this.setState({ customers: data  })
                },
                (error) => {
                    console.log(error, 'error--------------')
                   
                }
            )  
  }


  toggle() {
    this.setState(prevState => ({
      modal: !prevState.modal,
    }))
  }

  handleCustomerClicks = arg => {
    this.setState({ selectedCustomer: arg })
    this.props.history.push("/add-user")
    this.toggle()
  }
  handleEdit = id =>{
    console.log(id, "-----id")
    this.props.history.push(`/update-user/${id}`)

  }

  handleDelete = id => {
    const requestOptions = {
      method: 'DELETE',
      headers: { 'Content-Type': 'application/json' },
    };

    fetch(`${BaseUrl}/api/account/users/${id}`,requestOptions)
    .then(res => 
            {if(res.status===200 || res.status===204){
                res.json()
                .then(
                    this.props.history.push("/users")
                )
              }
              else{
                  res.json()
                  .then(
                      (data) =>{
                        console.log(data, "error")
                      }
                    )
                  }    
            }
          )
  }

  render() {
    //get Customets
    const { customers } = this.state
   
    // Columns
    const EcommerceCustomerColumns = [
      {
        dataField: "first_name",
        text: "Username",
        sort: true,
        formatter: (cellContent, row) => (
          <>
            <p className="mb-0">{`${row.first_name} ${row.last_name}`}</p>
          </>
        ),
      },
      {
        text: "Email",
        dataField: "email",
        sort: true,
        formatter: (cellContent, row) => (
          <>
            <p className="mb-0">{row.email}</p>
          </>
        ),
      },
      {
        text: "Phone",
        dataField: "phone",
        sort: true,
      },
      {
        dataField: "menu",
        isDummyField: true,
        text: "Action",
        formatter: (cellContent, row) => 
          { if (row.is_superuser){
            return ("")
          }else{
            return (
              <>
              <Link  to="#" onClick={() => this.handleEdit(row.id)} className="me-3 text-primary"><i className="mdi mdi-pencil font-size-18"></i></Link>
              <Link to="#" onClick={() => this.handleDelete(row.id)} className="text-danger"><i className="mdi mdi-trash-can font-size-18"></i></Link>
              </>)
          }
        }
      },
    ]


    //pagination customization
    const pageOptions = {
      sizePerPage: 10,
      totalSize: customers.length, // replace later with size(customers),
      custom: true,
    }

    const defaultSorted = [{
      dataField: 'id',
      order: 'desc'
    }];

    const { SearchBar } = Search

    const selectRow = {
      mode: 'checkbox'
    };

    return (
      <React.Fragment>

        <div className="page-content">
          
          <Container fluid>
          <Breadcrumbs title="Ecommerce" breadcrumbItems={this.state.breadcrumbItems} />

            <Row>
              <Col xs="12">
                <Card>
                  <CardBody>
                    <PaginationProvider
                      pagination={paginationFactory(pageOptions)}
                      keyField='id'
                      data={customers}
                    >
                      {({ paginationProps, paginationTableProps }) => (
                        <ToolkitProvider
                          keyField='id'
                          columns={EcommerceCustomerColumns}
                          data={customers}
                          search
                        >
                          {toolkitProps => (
                            <React.Fragment>
                              <Row>
                                <Col sm="4">
                                  <div className="search-box me-2 mb-2 d-inline-block">
                                    <div className="position-relative">
                                      <SearchBar
                                        {...toolkitProps.searchProps}
                                      />
                                      <i className="bx bx-search-alt search-icon" />
                                    </div>
                                  </div>
                                </Col>
                                <Col sm="8">
                                  <div className="text-sm-end">
                                    <Button
                                      type="button"
                                      color="success"
                                      className="btn-rounded mb-2 me-2"
                                      onClick={this.handleCustomerClicks}

                                    >
                                      <i className="mdi mdi-plus me-1" />{" "}
                                      New User
                                    </Button>
                                  </div>
                                </Col>
                              </Row>

                              <div className="table-responsive">
                                <BootstrapTable
                                  keyField={"id"}
                                  responsive
                                  bordered={false}
                                  striped={false}
                                  defaultSorted={defaultSorted}
                                  headerWrapperClasses={"thead-light"}
                                  {...toolkitProps.baseProps}
                                  {...paginationTableProps}
                                />

                              </div>
                              <div className="pagination pagination-rounded justify-content-end mb-2">
                                <PaginationListStandalone
                                  {...paginationProps}
                                />
                              </div>
                            </React.Fragment>
                          )}
                        </ToolkitProvider>
                      )}
                    </PaginationProvider>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}

EcommerceCustomers.propTypes = {
  customers: PropTypes.array,
  onGetCustomers: PropTypes.func
}

const mapStateToProps = ({ Ecommerce }) => ({
  customers: Ecommerce.customers,
})

const mapDispatchToProps = dispatch => ({
  onGetCustomers: () => dispatch(getCustomers())
})

export default connect(mapStateToProps, mapDispatchToProps)(EcommerceCustomers)