import React, { Component } from "react"
import PropTypes from "prop-types"
import { connect } from "react-redux"
import { withRouter } from "react-router-dom"

import BootstrapTable from "react-bootstrap-table-next"
import paginationFactory, {
  PaginationProvider,
  PaginationListStandalone,
} from "react-bootstrap-table2-paginator"
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit"

import { Button, Card, CardBody, Col, Container, Row} from "reactstrap"
import { Link } from "react-router-dom"
import { Badge } from "reactstrap"
import { BaseUrl } from "../../../helpers/Baseurl"

//Import Breadcrumb
import Breadcrumbs from '../../../components/Common/Breadcrumb';

import {
  getOrders
} from "../../../store/actions"

class EcommerceOrders extends Component {
  constructor(props) {
    super(props)
    this.state = {
      viewmodal: false,
      modal: false,
      orders: [],
      breadcrumbItems : [
        { title : "DynaminOutdoors", link : "/dashboard" },
        { title : "Jobs", link : "#" },
    ],
    }

    this.handleOrderClicks = this.handleOrderClicks.bind(this)
    this.toLowerCase1 = this.toLowerCase1.bind(this)
  }

  toLowerCase1(str) {
      return str.toLowerCase();
  }

  async componentDidMount() {
 
    const requestOptions = {
           method: 'GET',
           headers: { 'Content-Type': 'application/json' },
       };
       await fetch(`${BaseUrl}/api/jobs/jobs/`,requestOptions)
           .then(res => res.json())
           .then(
               (data) => {
                   this.setState({ orders: data  })
               },
               (error) => {
                   console.log(error, 'error--------------') 
               }
           )  
 }



  handleOrderClicks = () => {

    this.props.history.push("/new-job")

  }



  render() {
    const { orders } = this.state

    const { SearchBar } = Search

    //pagination customization
    let total_size = orders.length 
    const pageOptions = {
      sizePerPage: 10,
      totalSize: total_size, // replace later with size(Order),
      custom: true,
    }

    const defaultSorted = [{
      dataField: 'orderId',
      order: 'desc'
    }];

    const selectRow = {
      mode: 'checkbox',
    };

    //Columns
    const EcommerceOrderColumns = [
      {
        dataField: "name",
        text: "Name",
        sort: true,
      },
      {
        dataField: "labour_rate",
        text: "Labor Rate",
        sort: true,
        formatter: (cellContent, row) => (
          row.labour_rate
        ),
      },
      {
        dataField: "desired_gross_margin",
        text: "Desired Gross Margin",
        sort: true,
      },
      {
        dataField: "actual_gross_margin",
        text: "Actual Gross Margin",
        sort: true,
      },
      {
        dataField: "target_selling_price",
        text: "Target Selling Price",
        sort: true,
      },
      {
        dataField: "actual_selling_price",
        text: "Actual Selling Price",
        sort: true,
      },
      {
        dataField: "status",
        text: "Status",
        sort: true,
        formatter: (cellContent, row) => (
          row.status ==="todo"?(
            <Badge
            className={"font-size-12 badge-soft-success"}
            color={"success"}
            pill
          >
            {row.status}
          </Badge>
            ):(
              row.status ==="pending"?(
                <Badge
                  className={"font-size-12 badge-soft-warning"}
                  color={"warning"}
                  pill
                >
                  {row.status}
                </Badge>
                
                ):(
                  <Badge
                  className={"font-size-12 badge-soft-danger"}
                  color={"danger"}
                  pill
                >
                  {row.status}
                </Badge>
                  )

              )
            
          
         
        ),
      },
      {
        dataField: "action",
        isDummyField: true,
        text: "Action",
        formatter: (cellContent, row) => (
          <React.Fragment>
            <Link to="#" onClick={() => this.props.history.push(`/edit-job/${row.id}`) } className="me-3 text-primary"><i className="mdi mdi-pencil font-size-18"></i></Link>
            <Link to="#" className="text-danger"><i className="mdi mdi-trash-can font-size-18"></i></Link>
          </React.Fragment>
        ),
      },
    ]

    return (
      <React.Fragment>
        <div className="page-content">
          
          <Container fluid>
          <Breadcrumbs title="Job" breadcrumbItems={this.state.breadcrumbItems} />
            <Row>
              <Col xs="12">
                <Card>
                  <CardBody>
                    <PaginationProvider
                      pagination={paginationFactory(pageOptions)}
                      data={orders}
                    >
                      {({ paginationProps, paginationTableProps }) => (
                        <ToolkitProvider
                          keyField="id"
                          data={orders}
                          columns={EcommerceOrderColumns}
                          bootstrap4
                          search
                        >
                          
                          {toolkitProps => (
                            <React.Fragment>
                              
                              <Row className="mb-2">
                                <Col sm="4">
                                  <div className="search-box me-2 mb-2 d-inline-block">
                                    <div className="position-relative">
                                      <SearchBar
                                        {...toolkitProps.searchProps}
                                      />
                                      <i className="bx bx-search-alt search-icon" />
                                    </div>
                                  </div>
                                </Col>
                                <Col sm="8">
                                  <div className="text-sm-end">
                                    <Button
                                      type="button"
                                      color="success"
                                      className="btn-rounded mb-2 me-2"
                                      onClick={this.handleOrderClicks}
                                    >
                                      <i className="mdi mdi-plus me-1" />{" "}
                                      Add New Job
                                    </Button>
                                  </div>
                                </Col>
                              </Row>
                              <div className="table-responsive">

                                <BootstrapTable
                                  {...toolkitProps.baseProps}
                                  {...paginationTableProps}
                                  responsive
                                  bordered={false}
                                  striped={false}
                                  defaultSorted={defaultSorted}
                                  headerWrapperClasses={"table-light"}
                                />
                                
                              </div>
                              <div className="pagination pagination-rounded justify-content-end mb-2">
                                <PaginationListStandalone
                                  {...paginationProps}
                                />
                              </div>
                            </React.Fragment>
                          )}
                        </ToolkitProvider>
                      )}
                    </PaginationProvider>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}

EcommerceOrders.propTypes = {
  orders: PropTypes.array,
  onGetOrders: PropTypes.func
}

const mapStateToProps = state => ({
  orders: state.Ecommerce.orders,
})

const mapDispatchToProps = dispatch => ({
  onGetOrders: () => dispatch(getOrders())
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(EcommerceOrders))

