  async componentDidMount() {
    const id =this.props.match.params.id
    const requestOptions = {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' },
    };

    await fetch(`${BaseUrl}/api/jobs/jobs/${id}/`,requestOptions)
        .then(res => res.json())
        .then(
            (data) => {
                this.setState({ job: data.job  })
                this.setState({ Material_Budget: data.material_budget})
                this.setState({ Labour_Budget: data.labour_budget})
                this.setState({ Other_Budget: data.other_budget})
                this.setState({ Add_On_Budget: data.add_on_budget})

                if (data.job.status === "todo"){
                  this.setState({ selectedGroup:{label: "Todo", value: "todo" } })
                }else if (data.job.status === "pending"){
                  this.setState({ selectedGroup:{ label: "Pending", value: "pending" } })
                }
                else {
                  this.setState({ selectedGroup:{ label: "Completed", value: "completed" } })

                }
                
            },
            (error) => {
                console.log(error, 'error--------------')
            }
        )  
  
  }

  async MaterialBudgetSaveCell(oldValue, newValue, row, column){
    const id =this.props.match.params.id
    const row_id = ""+row.id;
    const strFirstThree = row_id.substring(0,3);
    let data = {
      "item": row.item,
      // "quantity": row.quantity,
      // "unit_price": row.unit_price,
      // "total_price": row.total_price,
      "job": id
    }
    
    if (row.quantity===""){
      data["quantity"] = null
    }else{
      data["quantity"] = row.quantity
    }
    if (row.unit_price===""){
      data["unit_price"] = null
    }else{
      data["unit_price"] = row.unit_price
    }
    if (row.total_price===""){
      data["total_price"] = null
    }else{
      data["total_price"] = row.total_price
    }

    if(strFirstThree === "new"){
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
        
        };
        fetch(`${BaseUrl}/api/jobs/material-budget/`,requestOptions)
              .then(res => 
                  {if(res.status === 400||res.status === 404 || res.status === 500){
                      res.json()
                      .then(
                          (data) =>{  
                            console.log(data, "---data")
                             
                          }
                      )
                  }
                  else{
                      res.json()
                      .then(
                          (data) =>{
                            const { Material_Budget } = this.state
                            let updated_data = this.state.Material_Budget
                            for (let i=0; i<Material_Budget.length;i++ ){
                              if (updated_data[i].id === row.id){
                                updated_data[i].id = data.id
                              }
                            }
                            this.setState({ Material_Budget:updated_data })
                          }
                      )
                  }    
              }
          )
    } 
    else{
      console.log("else")
      const requestOptions = {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
        
        };
        fetch(`${BaseUrl}/api/jobs/material-budget/${row_id}/`,requestOptions)
              .then(res => 
                  {if(res.status === 400||res.status === 404 || res.status === 500){
                      res.json()
                      .then(
                          (data) =>{  
                            console.log(data, "---data")
                            
                           
                            // toastr.error("Something Went Wrong", "")  
                          }
                      )
                  }
                  // else{
                  //     res.json()
                  //     .then(
                  //         (data) =>{
                                                       
                  //         }
                  //     )
                  // }    
              }
          )
    }
    

  }



